<template>
    <div>
        <el-table
            v-loading="loading"
            :data="list"
            style="width: 100%;">
            <el-table-column
                label="发布内容"
                prop="text"
                fixed>
            </el-table-column>
            <el-table-column
                label="发布图片">
                <template slot-scope="scope">
                    <!--图片查看插件嵌套element的轮播图组件-->
                    <viewer>
                        <el-carousel :interval="4000" type="card" height="200px" v-if="scope.row.pic_urls.length>0">
                            <el-carousel-item v-for="item in scope.row.pic_urls">
                                <img :src="item.thumbnail_pic|imageFilter">
                            </el-carousel-item>
                        </el-carousel>
                    </viewer>
                </template>
            </el-table-column>
            <el-table-column
                label="发布者"
                fixed>
                <template slot-scope="scope">
                    <img :src="scope.row.user.profile_image_url" v-if="scope.row.user.profile_image_url">
                    <span>{{scope.row.user.name}}</span>
                </template>
            </el-table-column>
            <el-table-column
                label="发布尾巴"
                with="300"
                :show-overflow-tooltip="true">
                <template slot-scope="scope">
                    <span v-html="scope.row.source"></span>
                </template>
            </el-table-column>
            <el-table-column
                label="发布时间"
                prop="created_at">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span>{{ new Date(scope.row.created_at) | parseTime }}</span>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
            :page-size="query.limit"
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :total="total">
        </el-pagination>
    </div>
</template>

<script>
    import {list} from "../../api/wb/wb";

    export default {
        data() {
            return {
                query: {
                    site_id: "",
                    page: 1,
                    limit: 20
                },
                total: 0,
                loading: true,
                list: [],
            };
        },
        components: {
        },
        methods: {
            onReset() {
                this.$router.push({
                    path: ""
                });
                this.query = {
                    site_id: "",
                    page: 1,
                    limit: 20
                };
                this.getList();
            },
            onSubmit() {
                this.$router.push({
                    path: "",
                    query: this.query
                });
                this.getList();
            },
            handleSizeChange(val) {
                this.query.limit = val;
                this.getList();
            },
            handleCurrentChange(val) {
                this.query.page = val;
                this.getList();
            },
            getList(name) {
                let params = {
                    name: name
                }
                list(params)
                    .then(response => {
                        console.log(response)
                        this.loading = false;
                        this.list = response.data.statuses || [];
                        this.total = response.data.total || 0;
                        console.log(this.list)
                    })
                    .catch(() => {
                        this.loading = false;
                        this.list = [];
                        this.total = 0;
                    });
            },
        },
        filters: {
            imageFilter(image) {
                return  image.replace('thumbnail','large')
            }
        },
        mounted() {
            document.body.ondrop = function (event) {
                event.preventDefault();
                event.stopPropagation();
            };
        },
        created() {
            // 将参数拷贝进查询对象
            let query = this.$route.query;
            this.query = Object.assign(this.query, query);
            this.query.limit = parseInt(this.query.limit);
            this.getList('刘抱');
        }
    };
</script>

