import axios from "../../utils/axios";
// 列表
export function list(query) {
    return axios({
        url: "/weibo/list",
        method: "get",
        params: query
    });
}

// 最新微博列表
export function home(query) {
    return axios({
        url: "/weibo/home",
        method: "get",
        params: query
    });
}
